import { html } from 'lit';
import { property, query } from 'lit/decorators.js';
import { watch } from '../../internal/watch';
import styles from './list.styles';
import UIKitElement from '../../internal/uikit-element';
import type { CSSResultGroup } from 'lit';
import type ListItem from '../list-item/list-item.component';

/**
 * @summary List is used for displaying single-level information/data.
 * @documentation https://kind-desert-0fb80550f.5.azurestaticapps.net/?path=/docs/components-list--docs
 *
 * @slot - The list's content, includes only list items
 *
 * @csspart base - The component's base wrapper.
 */
export default class List extends UIKitElement {
  static styles: CSSResultGroup = styles;

  @query('slot:not([name])') defaultSlot: HTMLSlotElement;

  /** The list's size. This size will be applied to all child list-items. */
  @property({ reflect: true }) size: 'small' | 'medium' | 'large' = 'medium';

  connectedCallback() {
    super.connectedCallback();
    this.setAttribute('role', 'list');
  }

  private getAllLists() {
    return [...this.querySelectorAll<ListItem>('koerber-list-item')];
  }

  private async syncListElements() {
    const list_items = this.getAllLists();

    await Promise.all(
      // Sync the size for child list items
      list_items.map(async list_item => {
        await list_item.updateComplete;
        list_item.size = this.size;
      })
    );
  }

  @watch('size', { waitUntilFirstUpdate: true })
  handleSizeChange() {
    this.syncListElements();
  }

  render() {
    return html`
      <div part="base" class="list">
        <slot @slotchange=${this.syncListElements}></slot>
      </div>
    `;
  }
}
